body::after {
	content: "";
	background-color: $white;
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0;
	transition: opacity 0.8s ease 0s;
	pointer-events: none;
	z-index: 149;
}
.popup-show body::after {
	opacity: 1;
	pointer-events: auto;
}

.popup {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	padding: 100px;
	transition: visibility 0.3s ease 0s;
	visibility: hidden;
	pointer-events: none;
	&.popup_show {
		z-index: 150;
		visibility: visible;
		overflow: auto;
		pointer-events: auto;
	}
	@media (max-width: $pc) {
		padding: 80px 50px;
	}
	@media (max-width: $tablet) {
		padding: 60px 16px;
	}
	// .popup__wrapper
	&__wrapper {
		min-height: 100%;
		flex: 1 1 auto;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	// .popup__content
	&__content {
		visibility: hidden;
		transform: scale(0);
		transition: transform 0.3s ease 0s;
		background-color: $white;
		width: 100%;
		max-width: 1258px;
		.lock & {
			visibility: visible;
		}
		.popup-show & {
			visibility: visible;
			transform: scale(1);
		}
	}
	// .popup__close
	&__close {
		position: absolute;
		top: 60px;
		right: 60px;
		@media (max-width: $pc) {
			top: 30px;
			right: 30px;
		}
	}
	// .popup__body
	&__body {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin: 0px 0px 0px 30px;
		@media (max-width: $tablet) {
			flex-direction: column;
			justify-content: start;
			margin: 0px 0px 0px 0px;
		}
	}
	// .popup__column
	&__column {
		@media (min-width: $tablet) {
			flex: 1 1 calc(50% - 30px);
			margin: 0px 60px 0px 0px;
			&:first-child {
				&.line {
					&::before {
						content: "";
						position: absolute;
						top: 30px;
						left: -32px;
						width: 2px;
						height: calc(100% - 60px);
						background-color: $mainColor;
					}
				}
			}
			&:nth-child(-n + 2) {
				.line-2 {
					border-bottom: 2px solid $mainColor;
					border-top: 2px solid $mainColor;
				}
			}
			&.line {
				&::after {
					content: "";
					top: 30px;
					right: -32px;
					height: calc(100% - 60px);
					background-color: $mainColor;
				}
			}
		}
		@media (max-width: $tablet) {
			&::after {
				content: "";
				display: none;
			}
		}
	}
	// .popup__item
	&__item {
		height: 100%;
		padding: 60px 30px;
		&.line-2 {
			border-bottom: 2px solid $mainColor;
			border-top: none;
		}
		@media (max-width: $pc) {
			padding: 30px;
		}
		@media (max-width: $tablet) {
			padding: 20px 0px 20px 0px;
		}
	}
	// .popup__titles
	&__titles {
		h2 {
			margin: 0px 0px 30px 0px;
			@media (max-width: $mobile) {
				margin: 0px 0px 20px 0px;
			}
		}
	}
	// .popup__text
	&__text {
		p {
			display: flex;
			flex-direction: column;
			font-weight: 600;
			font-size: 18px;
			line-height: 150%;
			margin: 0px 0px 24px 0px;
			span {
				font-weight: 700;
			}
			&:last-child {
				margin: 0px 0px 0px 0px;
			}
			@media (max-width: $mobile) {
				font-size: 16px;
			}
		}
	}
	// .popup__forms
	&__forms {
	}
	// .popup__form
	&__form {
	}
}

.form-popup {
	// .form-popup__inputs
	&__inputs {
		margin: -8px 0px;
	}
	// .form-popup__input
	&__input {
		padding: 8px 0px;
		display: flex;
		flex-direction: column;
		label {
			display: block;
			font-weight: 400;
			font-size: 14px;
			line-height: 120%;
			margin: 0px 0px 8px 0px;
			span {
				color: #e30004;
			}
			@media (max-width: $mobile) {
				font-size: 12px;
			}
		}
		input {
			width: 100%;
			padding: 15px 20px;
			border: 1px solid rgba(68, 76, 87, 0.2);
			font-weight: 400;
			font-size: 16px;
			line-height: 120%;
			&::placeholder {
				color: rgba(68, 76, 87, 0.4);
			}
			&._form-focus {
				border: 1px solid $orange;
			}
			&._form-error {
				border: 1px solid #e30004;
			}
			@media (max-width: $mobile) {
				font-size: 14px;
			}
		}
		textarea {
			height: 110px;
			resize: none;
			padding: 15px 20px;
			border: 1px solid rgba(68, 76, 87, 0.2);
			font-weight: 400;
			font-size: 16px;
			line-height: 120%;
			&::placeholder {
				color: rgba(68, 76, 87, 0.4);
			}
			&._form-focus {
				border: 1px solid $orange;
			}
			&._form-error {
				border: 1px solid #e30004;
			}
			@media (max-width: $mobile) {
				font-size: 14px;
			}
		}
		.form__error {
			font-weight: 400;
			font-size: 14px;
			line-height: 120%;
			color: #e30004;
			@media (max-width: $mobile) {
				font-size: 12px;
			}
		}
	}
	// .form-popup__select
	&__select {
	}
	// .form-popup__buttons
	&__buttons {
		margin: 30px 0px 0px 0px;
		@media (max-width: $mobile) {
			margin: 20px 0px 0px 0px;
		}
	}
	// .form-popup__button
	&__button {
		width: 100%;
	}
	// .form-popup__text
	&__text {
		margin: 16px 0px 0px 0px;
		font-weight: 400;
		font-size: 16px;
		line-height: 150%;
		a {
			color: $orange;
			transition: color 0.3s ease 0s;
			&:hover {
				color: $orangeHover;
			}
		}
		@media (max-width: $mobile) {
			margin: 10px 0px 0px 0px;
			font-size: 14px;
		}
	}
}


