//Документы
.tabs-documents {
	// .tabs-documents__navigation
	&__navigation {
	}
	// .tabs-documents__title
	&__title {
		white-space: nowrap;
		font-weight: 600;
		font-size: 16px;
		line-height: 100%;
		text-transform: uppercase;
		transition: color 0.3s ease 0s;
		padding: 0px 0px 5px 0px;
		border-bottom: 1px solid transparent;
		margin: 0px 30px 0px 0px;
		&:hover {
			color: $orange;
		}
		&:last-child {
			margin: 0px 0px 0px 0px;
		}
		&._tab-active {
			color: $orange;
			padding: 0px 0px 5px 0px;
			border-bottom: 1px solid $orange;
		}
	}
	// .tabs-documents__content
	&__content {
		padding: 50px 0px 0px 0px;
		@media (max-width: $mobile) {
			padding: 30px 0px 0px 0px;
		}
	}
	// .tabs-documents__body
	&__body {
	}
	// .tabs-documents__columns
	&__columns {
		display: flex;
		flex-wrap: wrap;
		margin: -15px;
		@media (max-width: $mobile) {
			flex-direction: column;
			margin: -10px 0px;
		}
	}
	// .tabs-documents__column
	&__column {
		flex: 0 1 33.3333%;
		padding: 15px;
		@media (max-width: $tablet) {
			flex: 0 1 50%;
		}
		@media (max-width: $mobile) {
			flex: 1 1 auto;
			padding: 10px 0px;
		}
	}
	// .tabs-documents__item
	&__item {
		background: #f4f4f4;
		padding: 40px;
		display: flex;
		flex-direction: column;
		height: 100%;
		min-height: 227px;
		@media (max-width: $mobile) {
			padding: 30px;
			min-height: 175px;
		}
	}
	// .tabs-documents__name
	&__name {
		flex: 1 1 auto;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		margin: 0px 0px 30px 0px;
		@media (max-width: $mobile) {
			font-size: 16px;
		}
	}
	// .tabs-documents__subname
	&__subname {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		margin: -5px;
	}
	// .tabs-documents__format
	&__format {
		padding: 5px;
		display: flex;
		align-items: center;
		font-weight: 700;
		font-size: 18px;
		line-height: 150%;
		color: $orange;
		transition: color 0.3s ease 0s;
		svg {
			margin: 0px 8px 0px 0px;
			path {
				transition: fill 0.3s ease 0s;
			}
		}
		&:hover {
			color: $orangeHover;
			svg {
				path {
					fill: $orangeHover;
				}
			}
		}
	}
	// .tabs-documents__size
	&__size {
		padding: 5px;
		font-weight: 600;
		font-size: 18px;
		line-height: 150%;
		color: rgba(68, 76, 87, 0.5);
	}
}

//Информация по домам
.tabs-house-info {
	// .tabs-house-info__navigation
	&__navigation {
	}
	// .tabs-house-info__title
	&__title {
		white-space: nowrap;
		flex: 0 1 auto;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		text-transform: uppercase;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		margin: 0px 30px 0px 0px;
		padding: 30px;
		background: #f4f4f4;
		transition:
			color 0.3s ease 0s,
			background-color 0.3s ease 0s;
		svg {
			margin: 0px 0px 0px 8px;
			path {
				transition: color 0.3s ease 0s;
			}
		}
		@media (max-width: $mobile) {
			padding: 24px;
			margin: 0px 16px 0px 0px;
		}
		&:last-child {
			margin: 0px 0px 0px 0px;
		}
		&:hover {
			color: $white;
			background: $orange;
			svg {
				path {
					fill: $white;
				}
			}
		}
		&._tab-active {
			color: $white;
			background: $orange;
			svg {
				path {
					fill: $white;
				}
			}
		}
	}
	// .tabs-house-info__content
	&__content {
	}
	// .tabs-house-info__body
	&__body {
		padding: 50px 0px 0px 0px;
		@media (max-width: $mobile) {
			padding: 30px 0px 0px 0px;
		}
	}
	// .tabs-house-info__items
	&__items {
	}
}
