//<BUTTONS (a.btn+tab)>
// Снипет (HTML): btn

.button-orange {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	flex: 0 0 44px;
	width: 44px;
	height: 44px;
	padding: 9px;
	background-color: $orange;
	transition: background-color 0.3s ease 0s;
	&:hover {
		background-color: #bd5b15;
	}
}

.button {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 26px 36px;
	background-color: $orange;
	transition: background-color 0.3s ease 0s;
	font-weight: 700;
	font-size: 18px;
	line-height: 100%;
	text-transform: uppercase;
	color: $white;
	svg {
		margin: -3px 0px 0px 8px;
	}
	&:hover {
		background-color: #bd5b15;
	}
}
