//О компании
.about {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .about__container
    &__container {
    }
    // .about__content
    &__content {
    }
    // .about__breadcrumbs
    &__breadcrumbs {
        margin: 0px 0px 60px 0px;
        ul {
            li {
                span {
                    color: rgba(255, 255, 255, 0.5);
                }
                a {
                    color: $white;
                    span {
                        color: $white;
                    }
                }
            }
        }
    }
    // .about__body
    &__body {
        display: flex;
        justify-content: space-between;
        width: 100%;
        .line-2 {
            border-bottom: none;
        }
        @media (max-width: $tablet) {
            flex-direction: column;
        }
    }
    // .about__column
    &__column {
        flex: 1 1 calc(43.3% - 30px);
        min-height: 450px;
        margin: 0px 30px 0px 0px;
        &:first-child {
            flex: 1 1 calc(58% - 30px);
        }
        &:last-child {
            margin: 0px 0px 0px 0px;
            &::after {
                content: "";
                display: none;
            }
        }
        &:nth-child(2n) {
            margin: 0px 0px 0px 0px;
            &::after {
                content: "";
                display: none;
            }
        }
        @media (max-width: $tablet) {
            min-height: 100%;
            flex: 1 1 auto;
            margin: 0px 0px 0px 0px;
            &:first-child {
                flex: 1 1 auto;
            }
            &::after {
                content: "";
                display: none;
            }
        }
        h2 {
            padding: 50px;
            @media (max-width: $tablet) {
                padding: 50px 0px;
            }
            @media (max-width: $tablet) {
                padding: 24px 0px;
            }
        }
        h1 {
            padding: 50px;
            @media (max-width: $pc) {
                padding: 50px 0px;
            }
            @media (max-width: $tablet) {
                padding: 0px 0px 60px 0px;
            }
        }
    }
    // .about__subtitle
    &__subtitle {
        display: flex;
        flex-direction: column;
        align-items: start;
        padding: 50px 50px 50px 40px;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 24px 0px;
        }
    }
    // .about__text
    &__text {
        p {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            margin: 0px 0px 28px 0px;
            &:last-child {
                margin: 0px 0px 0px 0px;
            }
            @media (max-width: $mobile) {
                font-size: 16px;
                margin: 0px 0px 26px 0px;
            }
        }
    }
    // .about__link
    &__link {
        margin: 30px 0px 0px 0px;
        padding: 0px 0px 5px 0px;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        text-transform: uppercase;
        color: $orange;
        border-bottom: 1px solid $orange;
        transition: border-bottom 0.3s ease 0s;
        &:hover {
            border-bottom: 1px solid transparent;
        }
    }
    // .about__numbers
    &__numbers {
    }
}
.numbers-about {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    // .numbers-about__column
    &__column {
        &:last-child {
            margin: 0px 0px 0px 0px;
            &::after {
                content: "";
                display: none;
            }
        }
        @media (min-width: $tablet) {
            flex: 1 1 calc(25% - 30px);
            margin: 0px 30px 0px 0px;
            &:nth-child(4n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 4) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $tablet) {
            width: calc(50% - 15px);
            margin: 0px 30px 0px 0px;
            &:nth-child(2n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 2) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
    }
    // .numbers-about__item
    &__item {
        height: 100%;
        padding: 50px;
        border-top: none;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 20px 0px;
        }
    }
    // .numbers-about__title
    &__title {
        font-weight: 600;
        font-size: 70px;
        line-height: 100%;
        letter-spacing: -0.06em;
        margin: 0px 0px 20px 0px;
        color: $orange;
        span {
            font-weight: 700;
            font-size: 50px;
            line-height: 110%;
        }
        @media (max-width: $pc) {
            font-size: 60px;
            span {
                font-size: 40px;
            }
        }
        @media (max-width: $mobile) {
            font-size: 40px;
            span {
                font-size: 20px;
            }
            svg {
                width: 20px;
                height: 20px;
            }
        }
    }
    // .numbers-about__subtitle
    &__subtitle {
        font-weight: 500;
        font-size: 18px;
        line-height: 150%;
        @media (max-width: $mobile) {
            font-size: 14px;
        }
        @media (max-width: 375px) {
            word-break: break-all;
        }
    }
}

//========================================================================================================================================================

//Баннер
.about-banner {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .about-banner__container
    &__container {
    }
    // .about-banner__image
    &__image {
        img {
            width: 100%;
            height: 528px;
            object-fit: cover;
            @media (max-width: $tablet) {
                height: 350px;
            }
            @media (max-width: $mobile) {
                height: 230px;
            }
        }
    }
}

//========================================================================================================================================================

//Принципы работы
.principles {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .principles__container
    &__container {
    }
    // .principles__body
    &__body {
        display: flex;
        justify-content: space-between;
        width: 100%;
        @media (max-width: $tablet) {
            flex-direction: column;
            justify-content: start;
        }
    }
    // .principles__column
    &__column {
        @media (min-width: $tablet) {
            flex: 1 1 calc(50% - 15px);
            margin: 0px 30px 0px 0px;
            &:last-child {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(2n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 2) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $tablet) {
            &::after {
                content: "";
                display: none;
            }
            &:first-child {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
    }
    // .principles__item
    &__item {
        padding: 50px;
        height: 100%;
        border-top: none;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 24px 0px;
        }
        ul {
            margin: -8px 0px;
            li {
                padding: 8px 0px;
                display: flex;
                font-weight: 400;
                font-size: 18px;
                line-height: 150%;
                @media (max-width: $mobile) {
                    font-size: 16px;
                }
                svg {
                    flex: 0 0 16px;
                    margin: 4px 16px 0px 0px;
                }
            }
        }
    }
}

//========================================================================================================================================================

//Ключевые задачи
.key-tasks {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .key-tasks__container
    &__container {
    }
    // .key-tasks__content
    &__content {
    }
    // .key-tasks__top
    &__top {
        display: flex;
        justify-content: space-between;
        width: 100%;
        .line-2 {
            border-bottom: none;
        }
        @media (max-width: $tablet) {
            flex-direction: column;
        }
    }
    // .key-tasks__header
    &__header {
        flex: 1 1 calc(33.33% - 30px);
        margin: 0px 30px 0px 0px;
        @media (max-width: $tablet) {
            flex: 1 1 auto;
            width: 100%;
            &.line {
                &::after {
                    content: "";
                    display: none;
                }
            }
        }
        h2 {
            padding: 50px 0px 50px 50px;
            height: 100%;
            @media (max-width: $pc) {
                padding: 50px 0px 50px 0px;
            }
            @media (max-width: $mobile) {
                padding: 24px 0px;
            }
        }
    }
    // .key-tasks__image
    &__image {
        flex: 1 1 calc(67.2% - 30px);
        @media (max-width: $tablet) {
            width: 100%;
            flex: 1 1 auto;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            padding: 50px;
            @media (max-width: $pc) {
                padding: 50px 0px;
            }
            @media (max-width: $mobile) {
                padding: 24px 0px;
            }
        }
    }
    // .key-tasks__body
    &__body {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        @media (max-width: $mobile) {
            flex-direction: column;
            justify-content: start;
        }
    }
    // .key-tasks__column
    &__column {
        min-height: 320px;
        &:last-child {
            margin: 0px 0px 0px 0px;
            &::after {
                content: "";
                display: none;
            }
        }
        @media (min-width: $tablet) {
            flex: 1 1 calc(33.33% - 30px);
            margin: 0px 30px 0px 0px;
            &:nth-child(3n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 3) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $tablet) and (min-width: $mobile) {
            width: calc(50% - 15px);
            margin: 0px 30px 0px 0px;
            &:nth-child(2n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 2) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $mobile) {
            flex: 1 1 auto;
            margin: 0px 0px 0px 0px;
            min-height: 100%;
            &::after {
                content: "";
                display: none;
            }
            &:first-child {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
    }
    // .key-tasks__item
    &__item {
        display: flex;
        position: relative;
        height: 100%;
        padding: 34px 50px 34px 50px;
        border-top: none;
        @media (max-width: $pc) {
            padding: 34px 0px 34px 0px;
        }
        @media (max-width: $mobile) {
            padding: 24px 0px;
        }
        svg {
            flex: 0 0 16px;
            margin: 6px 16px 0px 0px;
        }
        p {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            @media (max-width: $mobile) {
                font-size: 16px;
            }
        }
    }
}
