//Документы
.spollers-documents {
	// .spollers-documents__item
	&__item {
		border-top: 2px solid rgb(0, 0, 0, 0.05);
		border-bottom: 2px solid rgb(0, 0, 0, 0.05);
		padding: 50px 50px 50px 50px;
		@media (max-width: $pc) {
			padding: 50px 0px;
		}
		@media (max-width: $mobile) {
			padding: 20px 0px;
		}
	}
	// .spollers-documents__title
	&__title {
		&.h1 {
			color: $mainColor;
			flex-direction: row;
			align-items: center;
			transition: color 0.3s ease 0s;
			svg {
				margin: 0px 0px 0px 30px;
				path {
					transition: fill 0.3s ease 0s;
				}
				@media (max-width: $mobile) {
					width: 24px;
					height: 24px;
					margin: 0px 0px 0px 12px;
				}
			}
		}
		&:hover {
			color: $orange;
			svg {
				path {
					fill: $orange;
				}
			}
		}
		&._spoller-active {
			color: $orange;
			svg {
				path {
					fill: $orange;
				}
			}
		}
	}
	// .spollers-documents__body
	&__body {
		padding: 50px 0px 0px 0px;
		@media (max-width: $mobile) {
			padding: 30px 0px 0px 0px;
		}
	}
	// .spollers-documents__tabs
	&__tabs {
	}
}
