//Главный баннер
.banner {
    position: relative;
    min-height: 590px;
    margin: 0px 0px 100px 0px;
    @media (max-width: $tablet) {
        min-height: 460px;
    }
    @media (max-width: $mobile) {
        height: 100%;
        margin: 0px 0px 60px 0px;
    }
    // .banner__content
    &__content {
    }
    // .banner__breadcrumbs
    &__breadcrumbs {
        margin: 0px 0px 67px 0px;
        @media (max-width: $mobile) {
            margin: 0px 0px 40px 0px;
        }
        .breadcrambs__content {
            padding: 0px 0px 0px 0px;
        }
        ul {
            li {
                span {
                    color: rgba(255, 255, 255, 0.5);
                }
                a {
                    color: $white;
                    span {
                        color: $white;
                    }
                }
            }
        }
    }
    // .banner__slider
    &__slider {
        min-height: 590px;
        @media (max-width: $tablet) {
            min-height: 460px;
        }
        @media (max-width: $mobile) {
            height: 100%;
        }
    }
    // .banner__wrapper
    &__wrapper {
    }
    // .banner__slide
    &__slide {
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 3;
            background: linear-gradient(1deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 88.88%);
        }
    }
    // .banner__about
    &__about {
        position: relative;
        z-index: 10;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .line-2 {
            border-top: 2px solid $white;
            @media (max-width: $tablet) {
                border-top: none;
            }
        }
        .line {
            &::after {
                content: "";
                background: $white;
            }
        }
        .about__column {
            flex: 1 1 calc(50% - 30px);
            min-height: 100%;
            &:first-child {
                flex: 1 1 calc(50% - 30px);
            }
        }
        .about__subtitle {
            @media (max-width: $tablet) {
                padding: 0px 0px 0px 0px;
            }
        }
        .about__text {
            p {
                color: $white;
                @media (max-width: $mobile) {
                    font-size: 14px;
                }
            }
        }
    }
    // .banner__item
    &__item {
        position: relative;
        z-index: 10;
        padding: 166px 50px 100px 50px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: start;
        max-width: 70%;
        min-height: 590px;
        height: 100%;
        @media (max-width: $pc) {
            padding: 166px 0px 100px 0px;
        }
        @media (max-width: $tablet) {
            min-height: 500px;
            padding: 130px 0px 40px 0px;
            max-width: 80%;
        }
        @media (max-width: $mobileSmall) {
            max-width: 100%;
        }
    }
    // .banner__titles
    &__titles {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        align-items: start;
        svg {
            display: block;
            margin: 24px 0px 0px 0px;
            @media (max-width: $mobile) {
                margin: 16px 0px 0px 0px;
                width: 28px;
                height: 29px;
            }
        }
    }
    // .banner__location
    &__location {
        display: flex;
        font-weight: 400;
        font-size: 20px;
        line-height: 110%;
        color: $white;
        margin: 20px 0px 0px 0px;
        @media (max-width: $mobile) {
            margin: 10px 0px 0px 0px;
        }
        svg {
            margin: 0px 12px 0px 0px;
        }
    }
    // .banner__button
    &__button {
        margin: 50px 0px 0px 0px;
    }
    // .banner__pagination
    &__pagination {
        position: absolute;
        right: 0;
        width: 30%;
        bottom: 100px;
        display: flex;
        justify-content: flex-end;
        z-index: 10;
        padding: 0px 50px 0px 0px;
        @media (max-width: $pc) {
            padding: 0px 0px 0px 0px;
        }
        @media (max-width: $tablet) {
            justify-content: center;
            width: 100%;
            bottom: 40px;
            padding: 0px 15px;
        }
        .swiper-pagination-bullet {
            cursor: pointer;
            flex: 0 0 14px;
            height: 14px;
            background: rgba(255, 255, 255, 0.4);
            margin: 0px 14px 0px 0px;
            justify-content: center;
            width: calc(100% - 30px);
            left: 15px;
            bottom: 50px;
            left: 50%;
            transform: translate(-50%, 0px);
            &:last-child {
                margin: 0px 0px 0px 0px;
            }
        }
        .swiper-pagination-bullet-active {
            background: $white;
        }
    }
}

.main-home-banner {
    min-height: 800px;
    @media (max-width: $tablet) {
        min-height: 646px;
    }
    .banner__slider {
        min-height: 800px;
        @media (max-width: $tablet) {
            min-height: 646px;
        }
    }
    .banner__item {
        min-height: 800px;
        padding: 226px 20px 100px 50px;
        @media (max-width: $pc) {
            padding: 226px 0px 100px 0px;
        }
        @media (max-width: $tablet) {
            min-height: 646px;
            padding: 130px 0px 63px 0px;
        }
    }
    .banner__about {
        min-height: 800px;
        padding: 166px 0px 16px 0px;
        @media (max-width: $tablet) {
            min-height: 646px;
        }
    }
}

//========================================================================================================================================================

//Направления
.building {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .building__container
    &__container {
    }
    // .building__content
    &__content {
    }
    // .building__body
    &__body {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        @media (max-width: $tablet) {
            flex-direction: column;
            justify-content: start;
        }
    }
    // .building__column
    &__column {
        @media (min-width: $tablet) {
            flex: 1 1 calc(50% - 15px);
            margin: 0px 30px 0px 0px;
            &:last-child {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(2n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 2) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $tablet) {
            &::after {
                content: "";
                display: none;
            }
            &:first-child {
                .line-2 {
                    padding: 20px 0px 10px 0px;
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
            &:last-child {
                .line-2 {
                    padding: 10px 0px 20px 0px;
                    border-bottom: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
    }
    // .building__item
    &__item {
        padding: 50px;
        height: 100%;
        border-top: none;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 10px 0px;
            border-bottom: none;
            min-height: 318px;
        }
    }
    // .building__items
    &__items {
        cursor: pointer;
        overflow: hidden;
        position: relative;
        min-height: 456px;
        height: 100%;
        @media (max-width: $mobile) {
            min-height: 318px;
        }
        &._active {
            .building__before {
                opacity: 1;
                pointer-events: auto;
            }
            &::before {
                content: "";
                opacity: 1;
            }
        }
        &::before {
            content: "";
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 9;
            backdrop-filter: blur(8px);
            -webkit-backdrop-filter: blur(8px);
            will-change: transform;
            backface-visibility: hidden;
        }
    }
    // .building__front
    &__front {
        cursor: pointer;
        position: relative;
        height: 100%;
        @media (max-width: $mobile) {
            min-height: 318px;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            min-height: 456px;
            @media (max-width: $mobile) {
                min-height: 318px;
            }
        }
        &::before {
            content: "";
            cursor: pointer;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            background: linear-gradient(1deg, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.1) 53.41%);
        }
    }
    // .building__desc
    &__desc {
        position: absolute;
        top: 40px;
        left: 40px;
        width: calc(100% - 80px);
        height: calc(100% - 80px);
        z-index: 5;
        height: auto;
        @media (max-width: $mobile) {
            top: 30px;
            left: 20px;
            width: calc(100% - 40px);
            height: calc(100% - 60px);
        }
    }
    // .building__title
    &__title {
        font-weight: 400;
        font-size: 16px;
        line-height: 110%;
        text-transform: uppercase;
        color: $white;
        margin: 0px 0px 10px 0px;
        @media (max-width: $mobile) {
            font-size: 14px;
        }
    }
    // .building__name
    &__name {
        font-weight: 600;
        font-size: 44px;
        line-height: 110%;
        color: $white;
        @media (max-width: $mobile) {
            font-size: 28px;
        }
    }
    // .building__before
    &__before {
        min-height: 456px;
        opacity: 0;
        transition: opacity 0.3s ease 0s;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        background: rgba(68, 76, 87, 0.8);
        pointer-events: none;
        @media (max-width: $mobile) {
            min-height: 318px;
        }
    }
    // .building__list
    &__list {
        position: relative;
        z-index: 15;
        padding: 40px 150px 40px 40px;
        height: 100%;
        overflow: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
            display: none;
        }
        @media (max-width: 1820px){
            padding: 40px 130px 40px 30px;
        }
        @media (max-width: $mobile) {
            padding: 30px 100px 30px 30px;
        }
        @media (max-width: $mobileSmall) {
            padding: 10px 100px 10px 10px;
        }
        ul {
            margin: -10px -20px;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            @media (max-width: 1500px) {
                flex-direction: column;
                justify-content: start;
            }
            @media (max-width: $tablet) {
                flex-direction: row;
            }
            @media (max-width: $mobile) {
                flex-direction: column;
                margin: -10px 0px;
            }
            li {
                flex: 0 1 50%;
                padding: 10px 20px;
                @media (max-width: $mobile) {
                    flex: 1 1 auto;
                    padding: 10px 0px;
                }
                a {
                    &:hover {
                        .building__list-name {
                            color: $orange;
                            svg {
                                path {
                                    fill: $orange;
                                }
                            }
                        }
                        .building__location {
                            color: $orange;
                            svg {
                                path {
                                    fill: $orange;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    // .building__list-name
    &__list-name {
        display: flex;
        font-weight: 700;
        font-size: 16px;
        line-height: 110%;
        text-transform: uppercase;
        color: $white;
        margin: 0px 0px 10px 0px;
        transition: color 0.3s ease 0s;
        @media (max-width: $mobile) {
            font-size: 14px;
        }
        svg {
            margin: 0px 0px 0px 8px;
            path {
                transition: fill 0.3s ease 0s;
            }
        }
    }
    // .building__location
    &__location {
        display: flex;
        font-weight: 400;
        font-size: 16px;
        line-height: 110%;
        color: $white;
        transition: color 0.3s ease 0s;
        @media (max-width: $mobile) {
            font-size: 14px;
        }
        svg {
            margin: 0px 10px 0px 0px;
            path {
                transition: fill 0.3s ease 0s;
            }
        }
    }
    // .building__close
    &__close {
        cursor: pointer;
        position: absolute;
        z-index: 15;
        width: 120px;
        height: 120px;
        flex: 0 0 120px;
        bottom: 0;
        right: 0;
        @media (max-width: $mobile) {
            width: 80px;
            height: 80px;
            flex: 0 0 80px;
        }
    }
}
