	font-size: 70px;
	line-height: 110%;
	text-transform: uppercase;
	color: $white;
	@media (max-width: $tablet) {
		font-size: 56px;
	}
	@media (max-width: $mobile) {
		font-size: 40px;
	}
	@media (max-width: 375px) {
