h2 {
	font-weight: 600;
	font-size: 44px;
	line-height: 120%;
	span {
		color: $orange;
	}
	@media (max-width: $mobile) {
		font-size: 28px;
	}
}

h5 {
	font-weight: 600;
	font-size: 18px;
	line-height: 100%;
	text-transform: uppercase;
	color: rgba(68, 76, 87, 0.5);
	padding: 0px 50px 20px 50px;
	@media (max-width: $pc) {
		padding: 0px 0px 20px 0px;
	}
}

.buttons {
	display: flex;
	margin: -5px;
	// .buttons__button
	&__button {
		padding: 5px;
	}
}

//========================================================================================================================================================

//Линии
.line {
	position: relative;
	&::after {
		content: "";
		position: absolute;
		top: 16px;
		right: -18px;
		height: calc(100% - 32px);
		width: 2px;
		background-color: rgb(0, 0, 0, 0.05);
		@media (max-width: $tablet) {
			right: -16px;
		}
	}
}
.line-2 {
	border-top: 2px solid rgb(0, 0, 0, 0.05);
	border-bottom: 2px solid rgb(0, 0, 0, 0.05);
}

//========================================================================================================================================================

//Хлебные крошки
.breadcrambs {
	padding: 40px 0px 100px 0px;
	@media (max-width: $tablet) {
		padding: 24px 0px 40px 0px;
	}
	// .breadcrambs__container
	&__container {
	}
	// .breadcrambs__content
	&__content {
		padding: 0px 50px;
		@media (max-width: $pc) {
			padding: 0px 0px 0px 0px;
		}
		ul {
			display: flex;
			flex-wrap: wrap;
			margin: -5px 0px;
			li {
				padding: 5px 0px;
				span {
					font-weight: 600;
					font-size: 16px;
					line-height: 100%;
					text-transform: uppercase;
					color: rgba(68, 76, 87, 0.5);
				}
				a {
					transition: color 0.3s ease 0s;
					font-weight: 600;
					font-size: 16px;
					line-height: 100%;
					text-transform: uppercase;
					span {
						margin: 0px 14px;
						color: $mainColor;
						transition: color 0.3s ease 0s;
					}
					&:hover {
						color: $orange;
					}
				}
			}
		}
	}
}

//========================================================================================================================================================

//Скролл
.scroll {
	display: flex;
	flex-wrap: nowrap;
	overflow: auto;
	-ms-overflow-style: none;
	scrollbar-width: none;
	&::-webkit-scrollbar {
		display: none;
	}
	@media (max-width: 1820px) {
		margin: 0px -15px;
		padding: 0px 15px;
	}
}

