.select {
	position: relative;
	// .select__menu
	&__menu {
		position: relative;
		&._active {
			.select__icon {
				transform: rotate(-45deg);
				svg {
					path {
						fill: $orange;
					}
				}
			}
			.select__body {
				display: block;
			}
		}
	}
	// .select__btn
	&__btn {
		position: relative;
	}
	// .select__btntext
	&__btntext {
		position: relative;
		cursor: pointer;
		position: relative;
		display: flex;
		align-items: center;
		input {
			pointer-events: none;
			text-align: left;
			padding: 16px 50px 16px 20px;
		}
	}
	// .select__icon
	&__icon {
		position: absolute;
		right: 20px;
		top: 17px;
		svg {
			path {
				transition: fill 0.3s ease 0s;
			}
		}
	}
	// .select__body
	&__body {
		display: none;
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
		max-height: 242px;
		border: 1px solid rgba(68, 76, 87, 0.2);
		border-top: none;
		background: $white;
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 62px;
			z-index: 5;
			transition: opacity 0.3s ease 0s;
			pointer-events: auto;
			opacity: 1;
			background: linear-gradient(360deg, #fff 0%, rgba(255, 255, 255, 0) 100%);
		}
		.select-scroll & {
			&::after {
				content: "";
				opacity: 0;
				pointer-events: none;
			}
		}
	}
	// .select__options
	&__options {
		padding: 20px 40px 20px 20px;
	}
	// .select__option
	&__option {
		cursor: pointer;
		font-weight: 400;
		font-size: 16px;
		line-height: 120%;
		color: #000;
		transition: color 0.3s ease 0s;
		margin: 0px 0px 20px 0px;
		&:last-child {
			margin: 0px 0px 0px 0px;
		}
		&._active {
			color: $orange;
		}
		&:hover {
			color: $orangeHover;
		}
	}
	// .select__option-text
	&__option-text {
	}
}
