.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 100;
	transition: background-color 0.8s ease 0s;
	background-color: rgba(20, 24, 26, 0.4);
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		backdrop-filter: blur(10px);
	}
	.menu-open & {
		background-color: #14181a;
	}
	.search-open & {
		background-color: #14181a;
	}
	// .header__container
	&__container {
	}
	// .header__content
	&__content {
		position: relative;
		z-index: 10;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 16px 50px;
		@media (max-width: $pc) {
			padding: 16px 0px;
		}
	}
	// .header__logo
	&__logo {
		img {
			width: 120px;
			height: auto;
			@media (max-width: $mobile) {
				width: 94px;
			}
		}
		@media (max-width: $tablet) {
			order: 1;
			flex: 1 1 auto;
		}
	}
	// .header__menu
	&__menu {
		margin: 0px 30px;
		@media (min-width: $tablet) {
			.search-open & {
				display: none;
			}
		}
		@media (max-width: $tablet) {
			order: 3;
			margin: 0px 0px 0px 0px;
		}
	}
	// .header__contacts
	&__contacts {
		.search-open & {
			display: none;
		}
		@media (max-width: $tablet) {
			order: 2;
		}
	}
	// .header__search
	&__search {
		display: none;
		.search-open & {
			display: block;
		}
	}
}

.header-black {
	background: #14181a;
}

.menu {
	// .menu__icon
	&__icon {
		display: none;
		@media (max-width: $tablet) {
			display: flex;
			cursor: pointer;
			width: 54px;
			height: 54px;
			flex: 0 0 54px;
			transition: background-color 0.3s ease 0s;
			margin: 0px 0px 0px 10px;
			svg {
				transition: transform 0.3s ease 0s;
			}
			.menu-open & {
				background-color: #444c57;
				svg {
					transform: rotate(45deg);
				}
			}
			.search-open & {
				display: none;
			}
		}
	}
	// .menu__body
	&__body {
		@media (max-width: $tablet) {
			display: flex;
			flex-direction: column;
			justify-content: start;
			align-items: center;
			border-top: 1px solid rgb(255, 255, 255, 0.1);
			position: fixed;
			top: 0;
			right: -100%;
			width: 100%;
			height: 100%;
			min-height: 100vh;
			background: #14181a;
			z-index: 50;
			transition: right 0.3s ease 0s;
			overflow: auto;
			-ms-overflow-style: none;
			scrollbar-width: none;
			&::-webkit-scrollbar {
				display: none;
			}
			.menu-open & {
				right: 0;
			}
		}
		ul {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			margin: -5px -25px;
			@media (max-width: $pc) {
				margin: -5px -10px;
			}
			@media (max-width: $tablet) {
				width: 100%;
				padding: 46px 16px;
				flex-direction: column;
				align-items: center;
				margin: -15px 0px;
			}
			li {
				padding: 5px 25px;
				@media (max-width: $pc) {
					padding: 5px 10px;
				}
				@media (max-width: $tablet) {
					padding: 15px 0px;
				}
				a {
					display: flex;
					align-items: center;
					font-weight: 600;
					font-size: 16px;
					line-height: 100%;
					text-transform: uppercase;
					color: $white;
					transition: color 0.3s ease 0s;
					svg {
						margin: 0px 0px 0px 6px;
						path {
							transition: fill 0.3s ease 0s;
						}
					}
					&:hover {
						color: $orange;
						svg {
							path {
								fill: $orange;
							}
						}
					}
					@media (max-width: $tablet) {
						text-align: center;
					}
				}
			}
		}
	}
}

.contacts-header {
	display: flex;
	align-items: center;
	// .contacts-header__contacts
	&__contacts {
		display: flex;
		flex-direction: column;
		margin: 0px 40px 0px 0px;
		@media (max-width: $pc) {
			margin: 0px 20px 0px 0px;
		}
		@media (max-width: $tablet) {
			width: 100%;
			margin: 0px 0px 0px 0px;
			border-top: 1px solid rgb(255, 255, 255, 0.1);
			padding: 46px 15px;
			align-items: center;
		}
	}
	// .contacts-header__phone
	&__phone {
		white-space: nowrap;
		font-weight: 700;
		font-size: 22px;
		line-height: 100%;
		text-transform: uppercase;
		color: $white;
		margin: 0px 0px 12px 0px;
		transition: color 0.3s ease 0s;
		&:hover {
			color: $orange;
		}
		@media (max-width: $tablet) {
			margin: 0px 0px 20px 0px;
		}
	}
	// .contacts-header__email
	&__email {
		white-space: nowrap;
		font-weight: 500;
		font-size: 16px;
		line-height: 100%;
		color: $white;
		transition: color 0.3s ease 0s;
		&:hover {
			color: $orange;
		}
	}
	// .contacts-header__buttons
	&__buttons {
		.button-orange {
			width: 54px;
			height: 54px;
			flex: 0 0 54px;
		}
		.button-eye {
			@media (max-width: $mobile) {
				display: none;
			}
		}
	}
	// .contacts-header__personal-cabinet
	&__personal-cabinet {
		margin: 0px 0px 0px 10px;
		@media (max-width: $tablet) {
			margin: 0px 0px 0px 0px;
			padding: 46px 15px 60px 15px;
			display: flex;
			justify-content: center;
			border-top: 1px solid rgb(255, 255, 255, 0.1);
			width: 100%;
		}
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			border: 1px solid $white;
			background-color: transparent;
			font-weight: 500;
			font-size: 16px;
			line-height: 100%;
			text-transform: uppercase;
			color: $white;
			padding: 19px 27px;
			transition:
				background-color 0.3s ease 0s,
				border 0.3s ease 0s;
			&:hover {
				background-color: $orange;
				border: 1px solid $orange;
			}
		}
	}
}

//Поиск
.search-header {
	max-width: 1374px;
	width: 100%;
	margin: 0px 0px 0px 30px;
	@media (max-width: $tablet) {
		order: 2;
	}
	// .search-header__content
	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	// .search-header__icon
	&__icon {
		cursor: pointer;
		svg {
			margin: 0px 16px 0px 0px;
			@media (max-width: $mobile) {
				margin: 0px 10px 0px 0px;
				width: 16px;
				height: 16px;
			}
			path {
				transition: stroke 0.3s ease 0s;
			}
		}
		&:hover {
			svg {
				path {
					stroke: $orange;
				}
			}
		}
	}
	// .search-header__input
	&__input {
		display: flex;
		align-items: center;
		flex: 0 1 1159px;
		border-bottom: 1px solid rgba(255, 255, 255, 0.3);
		padding: 0px 0px 20px 0px;
		@media (max-width: $mobile) {
			padding: 0px 0px 5px 0px;
		}
		input {
			width: 100%;
			background: transparent;
			font-weight: 600;
			font-size: 16px;
			line-height: 100%;
			text-transform: uppercase;
			color: $white;
			&::placeholder {
				color: $white;
			}
			@media (max-width: $mobile) {
				font-size: 14px;
			}
		}
	}
	// .search-header__close
	&__close {
		cursor: pointer;
		margin: 0px 0px 0px 20px;
		@media (max-width: $mobileSmall) {
			margin: 0px 0px 0px 10px;
		}
		&:hover {
			svg {
				path {
					fill: $orange;
				}
			}
		}
		svg {
			path {
				transition: fill 0.3s ease 0s;
			}
			@media (max-width: $mobile) {
				width: 16px;
				height: 16px;
			}
		}
	}
}
