//объекты
.objects {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .objects__container
    &__container {
    }
    // .objects__content
    &__content {
        padding: 0px 50px;
        @media (max-width: $pc) {
            padding: 0px 0px 0px 0px;
        }
        h1 {
            color: $mainColor;
            margin: 0px 0px 50px 0px;
            @media (max-width: $mobile) {
                margin: 0px 0px 30px 0px;
            }
        }
    }
    // .objects__search
    &__search {
    }
    // .objects__filter
    &__filter {
    }
}

.search-objects {
    margin: 0px 0px 30px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 20px 0px;
    }
    // .search-objects__input
    &__input {
        position: relative;
        input {
            background: #f4f4f4;
            width: 100%;
            padding: 25px 75px 25px 30px;
            font-weight: 500;
            font-size: 18px;
            line-height: 150%;
            &::placeholder {
                color: rgba(68, 76, 87, 0.5);
            }
            @media (max-width: $mobile) {
                font-size: 16px;
                padding: 25px 75px 25px 20px;
            }
        }
    }
    // .search-objects__icon
    &__icon {
        position: absolute;
        top: 24px;
        right: 30px;
        cursor: pointer;
        svg {
            path {
                transition: stroke 0.3s ease 0s;
            }
        }
        &:hover {
            svg {
                path {
                    stroke: $orangeHover;
                }
            }
        }
    }
}

.filter {
    // .filter__navigation
    &__navigation {
    }
    // .filter__title
    &__title {
        flex: 1 1 auto;
        border-top: 2px solid rgba(0, 0, 0, 0.05);
        border-right: 2px solid rgba(0, 0, 0, 0.05);
        border-bottom: 2px solid rgba(0, 0, 0, 0.05);
        &:first-child {
            border-left: 2px solid rgba(0, 0, 0, 0.05);
        }
        span {
            white-space: nowrap;
            display: flex;
            justify-content: start;
            text-align: left;
            padding: 30px;
            font-weight: 600;
            font-size: 16px;
            line-height: 100%;
            text-transform: uppercase;
            transition: color 0.3s ease 0s;
            @media (max-width: $mobile) {
                padding: 30px 20px;
                font-size: 14px;
            }
        }
        &._active {
            span {
                color: $orange;
            }
        }
        &:hover {
            span {
                color: $orange;
            }
        }
    }
    // .filter__content
    &__content {
        margin: 50px 0px 0px 0px;
        @media (max-width: $mobile) {
            margin: 30px 0px 0px 0px;
        }
    }
    // .filter__body
    &__body {
        display: flex;
        flex-wrap: wrap;
        margin: -15px;
        @media (max-width: $mobile) {
            flex-direction: column;
            margin: -15px 0px;
        }
    }
    // .filter__products
    &__products {
        flex: 0 1 25%;
        padding: 15px;
        @media (max-width: $pc) {
            flex: 0 1 33.3333%;
        }
        @media (max-width: $tablet) {
            flex: 0 1 50%;
        }
        @media (max-width: $mobile) {
            flex: 1 1 auto;
            padding: 15px 0px;
        }
    }
    // .filter__column
    &__column {
    }
}
.filter-column {
    &._hide {
        display: none;
    }
}

.products {
    // .products__item
    &__item {
        &._active {
            .images-products__body {
                opacity: 1;
                pointer-events: auto;
            }
            .products__images {
                &::before {
                    content: "";
                    opacity: 1;
                }
            }
            .products__title {
                color: $orange;
                svg {
                    path {
                        fill: $orange;
                    }
                }
            }
        }
    }
    // .products__images
    &__images {
    }
    // .products__before
    &__before {
    }
    // .products__desc
    &__desc {
        cursor: pointer;
    }
    // .products__title
    &__title {
        display: flex;
        font-weight: 700;
        font-size: 16px;
        line-height: 110%;
        text-transform: uppercase;
        margin: 0px 0px 10px 0px;
        transition: color 0.3s ease 0s;
        svg {
            margin: 0px 0px 0px 8px;
            path {
                transition: fill 0.3s ease 0s;
            }
        }
        &:hover {
            color: $orange;
            svg {
                path {
                    fill: $orange;
                }
            }
        }
    }
    // .products__location
    &__location {
        display: flex;
        font-weight: 400;
        font-size: 16px;
        line-height: 110%;
        svg {
            margin: 0px 10px 0px 0px;
        }
    }
}
.images-products {
    position: relative;
    height: 300px;
    margin: 0px 0px 24px 0px;
    &::before {
        content: "";
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 9;
        backdrop-filter: blur(8px);
        -webkit-backdrop-filter: blur(8px);
        will-change: transform;
        backface-visibility: hidden;
    }
    // .images-products__image
    &__image {
        img {
            width: 100%;
            height: 300px;
            object-fit: cover;
        }
    }
    // .images-products__body
    &__body {
        opacity: 0;
        transition: opacity 0.3s ease 0s;
        position: absolute;
        padding: 30px;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        background: rgba(68, 76, 87, 0.8);
        pointer-events: none;
        overflow: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
            display: none;
        }
    }
    // .images-products__column
    &__column {
        margin: 0px 0px 30px 0px;
        &:last-child {
            margin: 0px 0px 0px 0px;
        }
    }
    // .images-products__title
    &__title {
        font-weight: 600;
        font-size: 14px;
        line-height: 120%;
        color: rgba(255, 255, 255, 0.5);
        margin: 0px 0px 16px 0px;
    }
    // .images-products__subtitles
    &__subtitles {
        a {
            margin: 12px 0px 0px 0px;
            font-weight: 500;
            font-size: 16px;
            line-height: 100%;
            color: $white;
            transition: color 0.3s ease 0s;
            &:hover {
                color: $orange;
            }
        }
    }
    // .images-products__name
    &__name {
        font-weight: 700;
        font-size: 18px;
        line-height: 100%;
        color: $white;
        margin: 0px 0px 12px 0px;
        &:last-child {
            margin: 0px 0px 0px 0px;
        }
    }
}

