//Информация по домам
.house-info {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .house-info__container
    &__container {
    }
    // .house-info__content
    &__content {
        border-top: 1px solid rgb(0, 0, 0, 0.05);
        border-bottom: 1px solid rgb(0, 0, 0, 0.05);
        padding: 50px;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 30px 0px;
        }
        .h1 {
            color: $mainColor;
            margin: 0px 0px 50px 0px;
            @media (max-width: $mobile) {
                margin: 0px 0px 30px 0px;
            }
        }
    }
    // .house-info__body
    &__body {
    }
    // .house-info__tabs
    &__tabs {
    }
}
.items-house-info {
    // .items-house-info__top
    &__top {
        margin: 0px 0px 40px 0px;
        @media (max-width: $mobile) {
            margin: 0px 0px 30px 0px;
        }
    }
    // .items-house-info__title
    &__title {
        font-weight: 600;
        font-size: 34px;
        line-height: 110%;
        margin: 0px 0px 40px 0px;
        @media (max-width: $mobile) {
            font-size: 24px;
            margin: 0px 0px 30px 0px;
        }
    }
    // .items-house-info__body
    &__body {
        display: flex;
        justify-content: space-between;
        width: 100%;
        @media (max-width: 1500px) {
            flex-wrap: wrap;
        }
        @media (max-width: $mobile) {
            flex-direction: column;
            justify-content: start;
            border-bottom: 2px solid rgb(0, 0, 0, 0.05);
        }
    }
    // .items-house-info__column
    &__column {
        @media (min-width: 1500px) {
            flex: 1 1 calc(16.66% - 30px);
            margin: 0px 30px 0px 0px;
            &:nth-child(1) {
                .items-house-info__item {
                    padding: 50px 50px 50px 0px;
                }
            }
            &:last-child {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
                .items-house-info__item {
                    padding: 50px 0px 50px 50px;
                }
            }
            &:nth-child(6n) {
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 6) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: 1500px) and (min-width: $mobile) {
            flex: 1 1 calc(33.33% - 30px);
            margin: 0px 30px 0px 0px;
            &:last-child {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(3n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 3) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $mobile) {
            border-top: none;
            border-bottom: none;
            margin: 0px 0px 30px 0px;
            &::after {
                content: "";
                display: none;
            }
        }
    }
    // .items-house-info__item
    &__item {
        position: relative;
        height: 100%;
        padding: 50px;
        border-top: none;
        display: flex;
        flex-direction: column;
        height: 100%;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 30px 0px;
        }
        @media (max-width: $mobile) {
            padding: 0px 0px 0px 0px;
            border-bottom: none;
        }
    }
    // .items-house-info__name
    &__name {
        font-weight: 600;
        font-size: 18px;
        line-height: 120%;
        margin: 0px 0px 30px 0px;
        @media (max-width: $mobile) {
            margin: 0px 0px 16px 0px;
        }
    }
    // .items-house-info__subname
    &__subname {
        font-weight: 600;
        font-size: 24px;
        line-height: 100%;
    }
    // .items-house-info__tabs-documents
    &__tabs-documents {
    }
}
