.footer {
    background: #444c57;
    // .footer__container
    &__container {
    }
    // .footer__content
    &__content {
    }
    // .footer__top
    &__top {
    }
    // .footer__bottom
    &__bottom {
    }
}

.top-footer {
    padding: 50px 0px 16px 0px;
    @media (max-width: $mobile) {
        padding: 0px;
    }
    // .top-footer__body
    &__body {
        display: flex;
        justify-content: space-between;
        @media (max-width: 1200px) {
            flex-wrap: wrap;
            justify-content: center;
            margin: -20px;
        }
        @media (max-width: $mobile) {
            flex-direction: column;
            align-items: center;
            margin: 0px 0px 0px 0px;
        }
    }
    // .top-footer__column
    &__column {
        flex: 1 1 auto;
        padding: 50px 50px 50px 50px;
        border-right: 1px solid rgb(255, 255, 255, 0.1);
        &:last-child {
            border-right: none;
        }
        .button {
            @media (max-width: 1750px) {
                padding: 26px 15px;
                width: 100%;
            }
            @media (max-width: $mobile) {
                padding: 26px 36px;
                width: auto;
            }
        }
        @media (max-width: 1750px) {
            flex: 0 1 25%;
        }
        @media (max-width: $pc) {
            padding: 50px 15px 50px 15px;
        }
        @media (max-width: 1200px) {
            flex: 0 1 33.333%;
            padding: 20px;
            border-right: none;
            display: flex;
            justify-content: start;
            align-items: center;
        }
        @media (max-width: $mobile) {
            width: 100%;
            justify-content: center;
            flex: 1 1 auto;
            padding: 30px 0px;
            border-bottom: 1px solid rgb(255, 255, 255, 0.1);
        }
    }
    // .top-footer__logo
    &__logo {
        img {
            width: 179px;
            height: auto;
        }
    }
    // .top-footer__item
    &__item {
        padding: 0px 50px;
        @media (max-width: 1750px) {
            padding: 0px 0px 0px 0px;
        }
    }
    // .top-footer__title
    &__title {
        font-weight: 600;
        font-size: 18px;
        line-height: 100%;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.5);
        margin: 0px 0px 30px 0px;
        @media (max-width: $mobile) {
            text-align: center;
        }
    }
    // .top-footer__lists
    &__lists {
        ul {
            margin: -10px 0px;
            li {
                padding: 10px 0px;
                @media (max-width: $mobile) {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
                a {
                    display: flex;
                    justify-content: start;
                    align-items: center;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 100%;
                    text-transform: uppercase;
                    color: $white;
                    transition: color 0.3s ease 0s;
                    svg {
                        margin: -2px 0px 0px 6px;
                        path {
                            transition: fill 0.3s ease 0s;
                        }
                    }
                    &:hover {
                        color: $orange;
                        svg {
                            path {
                                fill: $orange;
                            }
                        }
                    }
                    @media (max-width: $mobile) {
                        text-align: center;
                    }
                }
            }
        }
    }
    // .top-footer__buttons
    &__buttons {
        padding: 20px 0px 0px 0px;
        @media (max-width: $mobile) {
            justify-content: center;
        }
        .button-orange {
            background: $white;
            &:hover {
                background: $orange;
                svg {
                    path {
                        fill: $white;
                    }
                }
            }
        }
    }
}

.bottom-footer {
    display: flex;
    justify-content: space-between;
    padding: 50px;
    border-top: 1px solid rgb(255, 255, 255, 0.1);
    @media (max-width: $pc) {
        padding: 50px 0px;
    }
    @media (max-width: $mobile) {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        padding: 30px 0px;
    }
    a {
        white-space: nowrap;
        text-align: right;
        font-weight: 500;
        font-size: 18px;
        line-height: 150%;
        color: $white;
        transition: color 0.3s ease 0s;
        margin: 0px 0px 0px 30px;
        &:hover {
            color: $orange;
        }
        @media (max-width: $mobile) {
            white-space: wrap;
            text-align: center;
            margin: 0px 0px 30px 0px;
        }
    }
    // .bottom-footer__copy
    &__copy {
        font-weight: 500;
        font-size: 18px;
        line-height: 150%;
        color: $white;
        @media (max-width: $mobile) {
            text-align: center;
        }
    }
}
