//Услуги
.services {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .services__container
    &__container {
    }
    // .services__body
    &__body {
    }
    // .services__columns
    &__columns {
        display: flex;
        justify-content: space-between;
        width: 100%;
        @media (max-width: $tablet) {
            flex-direction: column;
            justify-content: start;
            margin: 0px 0px 30px 0px;
            &:last-child {
                margin: 0px 0px 0px 0px;
            }
        }
    }
    // .services__column
    &__column {
        @media (min-width: $tablet) {
            flex: 1 1 calc(67% - 15px);
            margin: 0px 30px 0px 0px;
            &:first-child {
                flex: 1 1 calc(33% - 15px);
            }
            &:last-child {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(2n) {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 2) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $tablet) {
            &::after {
                content: "";
                display: none;
            }
            &:first-child {
                .services__item {
                    border-bottom: none;
                    padding: 0px 0px 0px 0px;
                }
            }
        }
    }
    // .services__item
    &__item {
        padding: 50px;
        height: 100%;
        border-top: none;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 20px 0px 30px 0px;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        h2 {
            margin: 0px 0px 30px 0px;
            @media (max-width: $mobile) {
                margin: 0px 0px 20px 0px;
            }
        }
        ul {
            margin: -8px 0px;
            li {
                padding: 8px 0px;
                display: flex;
                font-weight: 400;
                font-size: 18px;
                line-height: 150%;
                @media (max-width: $mobile) {
                    font-size: 16px;
                }
                svg {
                    flex: 0 0 16px;
                    margin: 4px 16px 0px 0px;
                }
            }
        }
    }
}
