.main-contacts {
    h1 {
        color: $mainColor;
        margin: 0px 50px 100px 50px;
        @media (max-width: $pc) {
            margin: 0px 0px 100px 0px;
        }
        @media (max-width: $tablet) {
            margin: 0px 0px 30px 0px;
        }
    }
    .contacts__email {
        font-size: 34px;
    }
}

//========================================================================================================================================================

//Контакты
.contacts {
    margin: 0px 0px 140px 0px;
    @media (max-width: $mobile) {
        margin: 0px 0px 80px 0px;
    }
    // .contacts__container
    &__container {
    }
    // .contacts__content
    &__content {
    }
    // .contacts__body
    &__body {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        @media (max-width: $tablet) {
            flex-direction: column;
            justify-content: start;
        }
    }
    // .contacts__column
    &__column {
        @media (min-width: $tablet) {
            flex: 1 1 calc(33.33% - 30px);
            margin: 0px 30px 0px 0px;
            &:last-child {
                margin: 0px 0px 0px 0px;
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(3n) {
                &::after {
                    content: "";
                    display: none;
                }
            }
            &:nth-child(-n + 3) {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
        @media (max-width: $tablet) {
            &::after {
                content: "";
                display: none;
            }
            &:first-child {
                .line-2 {
                    border-top: 2px solid rgb(0, 0, 0, 0.05);
                }
            }
        }
    }
    // .contacts__item
    &__item {
        position: relative;
        height: 100%;
        padding: 50px;
        border-top: none;
        @media (max-width: $pc) {
            padding: 50px 0px;
        }
        @media (max-width: $tablet) {
            padding: 30px 0px;
        }
        a {
            text-align: left;
            word-break: break-word;
            font-weight: 600;
            font-size: 44px;
            line-height: 100%;
            transition:
                color 0.3s ease 0s,
                background 0.3s ease 0s;
            margin: 0px 0px 20px 0px;
            &:last-child {
                margin: 0px 0px 0px 0px;
            }
            &:hover {
                color: $orange;
            }
            @media (max-width: 1720px) {
                font-size: 36px;
            }
            @media (max-width: $mobile) {
                font-size: 28px;
            }
            @media (max-width: 375px) {
                font-size: 26px;
            }
        }
    }
    // .contacts__email
    &__email {
    }
    // .contacts__title
    &__title {
        font-weight: 600;
        font-size: 18px;
        line-height: 120%;
        margin: 0px 0px 30px 0px;
        @media (max-width: $mobile) {
            font-size: 16px;
            margin: 0px 0px 16px 0px;
        }
    }
    // .contacts__works
    &__works {
        margin: -10px 0px;
        @media (max-width: $mobile) {
            margin: -8px 0px;
        }
    }
    // .contacts__work
    &__work {
        display: flex;
        align-items: flex-end;
        padding: 10px 0px;
        font-weight: 600;
        font-size: 34px;
        line-height: 100%;
        @media (max-width: $mobile) {
            font-size: 28px;
            padding: 8px 0px;
        }
        span {
            white-space: nowrap;
            font-size: 24px;
            display: flex;
            margin: 0px 5px -2px 0px;
            @media (max-width: $mobile) {
                font-size: 18px;
            }
        }
    }
    // .contacts__buttons
    &__buttons {
    }
    // .contacts__manager
    &__manager {
        a {
            font-size: 34px;
            @media (max-width: $mobile) {
                font-size: 28px;
            }
        }
    }
    // .contacts__name
    &__name {
        font-weight: 600;
        font-size: 24px;
        line-height: 100%;
        margin: 0px 0px 20px 0px;
        @media (max-width: $mobile) {
            font-size: 18px;
            margin: 0px 0px 12px 0px;
        }
    }
}
