}
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  color: #444c57;
  line-height: 1.2;
  font-family: "VelaSans", sans-serif;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input,
button,
textarea {
  font-family: "VelaSans", sans-serif;
  font-size: inherit;
  outline: none;
}

button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
}

a {
  color: inherit;
}

a:link,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.lock body {
  overflow: hidden;
  touch-action: none;
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.wrapper > main {
  flex: 1 1 auto;
}
.wrapper > * {
  min-width: 0;
}

/*
(i) Стили будут применяться ко 
всем классам содержащим *__container
Например header__container, main__container и т.д.
Снипет (HTML): cnt
*/
[class*=__container] {
  max-width: 113.75rem;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 0.9375rem;
}

.button-orange {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex: 0 0 44px;
  width: 44px;
  height: 44px;
  padding: 9px;
  background-color: #ef7721;
  transition: background-color 0.3s ease 0s;
}
.button-orange:hover {
  background-color: #bd5b15;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 26px 36px;
  background-color: #ef7721;
  transition: background-color 0.3s ease 0s;
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  text-transform: uppercase;
  color: #ffffff;
}
.button svg {
  margin: -3px 0px 0px 8px;
}
.button:hover {
  background-color: #bd5b15;
}

.select {
  position: relative;
}
.select__menu {
  position: relative;
}
.select__menu._active .select__icon {
  transform: rotate(-45deg);
}
.select__menu._active .select__icon svg path {
  fill: #ef7721;
}
.select__menu._active .select__body {
  display: block;
}
.select__btn {
  position: relative;
}
.select__btntext {
  position: relative;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
}
.select__btntext input {
  pointer-events: none;
  text-align: left;
  padding: 16px 50px 16px 20px;
}
.select__icon {
  position: absolute;
  right: 20px;
  top: 17px;
}
.select__icon svg path {
  transition: fill 0.3s ease 0s;
}
.select__body {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  max-height: 242px;
  border: 1px solid rgba(68, 76, 87, 0.2);
  border-top: none;
  background: #ffffff;
}
.select__body::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 62px;
  z-index: 5;
  transition: opacity 0.3s ease 0s;
  pointer-events: auto;
  opacity: 1;
  background: linear-gradient(360deg, #fff 0%, rgba(255, 255, 255, 0) 100%);
}
.select-scroll .select__body::after {
  content: "";
  opacity: 0;
  pointer-events: none;
}
.select__options {
  padding: 20px 40px 20px 20px;
}
.select__option {
  cursor: pointer;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #000;
  transition: color 0.3s ease 0s;
  margin: 0px 0px 20px 0px;
}
.select__option:last-child {
  margin: 0px 0px 0px 0px;
}
.select__option._active {
  color: #ef7721;
}
.select__option:hover {
  color: #bd5b15;
}
body::after {
  content: "";
  background-color: #ffffff;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.8s ease 0s;
  pointer-events: none;
  z-index: 149;
}

.popup-show body::after {
  opacity: 1;
  pointer-events: auto;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 100px;
  transition: visibility 0.3s ease 0s;
  visibility: hidden;
  pointer-events: none;
}
.popup.popup_show {
  z-index: 150;
  visibility: visible;
  overflow: auto;
  pointer-events: auto;
}
@media (max-width: 1500px) {
  .popup {
    padding: 80px 50px;
  }
}
@media (max-width: 991.98px) {
  .popup {
    padding: 60px 16px;
  }
}
.popup__wrapper {
  min-height: 100%;
  flex: 1 1 auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup__content {
  visibility: hidden;
  transform: scale(0);
  transition: transform 0.3s ease 0s;
  background-color: #ffffff;
  width: 100%;
  max-width: 1258px;
}
.lock .popup__content {
  visibility: visible;
}
.popup-show .popup__content {
  visibility: visible;
  transform: scale(1);
}
.popup__close {
  position: absolute;
  top: 60px;
  right: 60px;
}
@media (max-width: 1500px) {
  .popup__close {
    top: 30px;
    right: 30px;
  }
}
.popup__body {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0px 0px 0px 30px;
}
@media (max-width: 991.98px) {
  .popup__body {
    flex-direction: column;
    justify-content: start;
    margin: 0px 0px 0px 0px;
  }
}
@media (min-width: 991.98px) {
  .popup__column {
    flex: 1 1 calc(50% - 30px);
    margin: 0px 60px 0px 0px;
  }
  .popup__column:first-child.line::before {
    content: "";
    position: absolute;
    top: 30px;
    left: -32px;
    width: 2px;
    height: calc(100% - 60px);
    background-color: #444c57;
  }
  .popup__column:nth-child(-n+2) .line-2 {
    border-bottom: 2px solid #444c57;
    border-top: 2px solid #444c57;
  }
  .popup__column.line::after {
    content: "";
    top: 30px;
    right: -32px;
    height: calc(100% - 60px);
    background-color: #444c57;
  }
}
@media (max-width: 991.98px) {
  .popup__column::after {
    content: "";
    display: none;
  }
}
.popup__item {
  height: 100%;
  padding: 60px 30px;
}
.popup__item.line-2 {
  border-bottom: 2px solid #444c57;
  border-top: none;
}
@media (max-width: 1500px) {
  .popup__item {
    padding: 30px;
  }
}
@media (max-width: 991.98px) {
